				span.message__text {{ msg.text }}
				router-link.message__link(:to="msg.link.to" v-if="msg.link") {{ msg.link.text }}
</template>

<script>
	export default {
		name: 'Message',
		props: ['message', 'lifetime'],
		data() {
			return {
				display: false,
				now: Date.now(),
				timer: null,
				left: null
			}
		},
		computed: {
			msg() {
				this.left = null;

				return this.message;
			},
			leftTime() {
				this.now;

				if(this.left == null) {
					this.left = this.lifetime;
				}

				if(this.left == 0) {
					this.close()
				}

				this.left--;

				return this.left;
			},

		},
		mounted() {
			this.timer = setInterval(() => {
				this.now = Date.now();
			}, 1000);

			this.display = true

			// if(this.lifetime) {
			// 	console.log('fifetime')
			// 	console.log(this.lifetime)
			// }

			// console.log('helllllo')
			// setTimeout(() => {

				// console.log('remove')
				// this.$store.commit('clearMsg')
			// }, 10000)
			// console.log(this.message)
		},
		beforeDestroy() {
			clearInterval(this.timer);
			this.timer = null;
		},
		methods: {
			close() {
				this.display = false
				
				setTimeout(() => {
					this.$store.commit('clearMsg')
				}, 500)
			}
		}
	}
</script>

